  width: 100%;
  height: 1.55rem;
  background-color: white;
  padding: 0.15rem;
  box-sizing: border-box;
  .info_title {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 0.18rem;
    .wujiaoWrapper {
      width: 0.25rem;
      height: 0.25rem;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .info_price {
    font-size: 0.19rem;
    color: orange;
    margin-top: 0.05rem;
  }
  .info_classify {
    color: gray;
    margin-top: 0.1rem;
  }
  .info_time {
    color: gray;
    margin-top: 0.1rem;
  }
}
.teach_team {
  width: 100%;
  height: 1.5rem;
  background-color: white;
  margin-top: 0.15rem;
  padding: 0.15rem;
  box-sizing: border-box;
  .teach_wrapper {
    width: 100%;
    height: 0.9rem;
    display: flex;
    .teach_left {
      flex: 1;
      height: 100%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      align-items: center;
      flex-direction: column;
      img {
        width: 0.35rem;
        height: 0.35rem;
        border-radius: 50%;
      }
      span {
        width: 100%;
        text-align: center;
        margin-top: 0.05rem;
        font-size: 0.14rem;
      }
    }
    .teach_right {
      flex: 4;
      height: 100%;
    }
  }
}
.classify_jieshao {
  width: 100%;
  height: 0.7rem;
  background-color: white;
  margin-top: 0.15rem;
  padding: 0.1rem;
  box-sizing: border-box;
}
.classify_outline {
  width: 100%;
  margin-top: 0.15rem;
  background-color: white;
  padding: 0.1rem;
  box-sizing: border-box;
  .classify_slide {
    width: 90%;
    height: 0.5rem;
    // background-color: violet;
    margin: 0.1rem auto;
    display: flex;
    align-items: center;
    margin-top: 0.05rem;
    .dian {
      width: 0.05rem;
      height: 0.05rem;
