          width: 0.4rem;
          height: 0.2rem;
          background-color: orange;
          border-radius: 0.02rem;
          color: white;
          font-size: 0.14rem;
          text-align: center;
        }
        .classify_title {
          margin-left: 0.1rem;
          font-size: 0.14rem;
          color: gray;
        }
      }
      .classify_slide_right_bottom {
        width: 100%;
        height: 40%;
        display: flex;
        align-items: center;
        span {
          margin-left: 0.1rem;
          font-size: 0.14rem;
