        }
      }
    }
  }
}
.classify_comment {
  width: 100%;
  margin-top: 0.15rem;
  background-color: white;
  padding: 0.1rem 0.1rem 1rem 0.1rem;
  box-sizing: border-box;

  box-sizing: border-box;
  .comment_wrapper {
    width: 100%;
    padding: 0.1rem;
    box-sizing: border-box;
    .comment_slide {
      width: 100%;
      height: 0.5rem;
      margin-top: 0.1rem;
      display: flex;
      justify-content: space-between;
      .slide_left {
        width: 0.25rem;
        height: 100%;
        // background-color: bisque;
        img {
          width: 0.25rem;
          height: 0.25rem;
          border-radius: 50%;
        }
      }
      .slide_center {
        width: 2.35rem;
        height: 100%;
        // background-color: blue;
        .slideCenter_top {
          width: 100%;
          height: 50%;
          span {
            margin: 0 0.1rem;
            font-size: 0.15rem;
          }
        }
        .slideCenter_bottom {
          width: 100%;
          height: 50%;
          span {
            margin: 0 0.1rem;
            font-size: 0.14rem;
            color: gray;
          }
        }
      }
      .slide_right {
        width: 1.3rem;
        height: 100%;
        // background-color: burlywood;
        color: gray;
        font-size: 0.14rem;
      }
    }
    .comment_none {
      width: 100%;
      height: 2rem;
      text-align: center;
      img {
        width: 1.6rem;
        height: 1.6rem;
      }
      div {
        color: rgb(82, 82, 82);
