      }
    }
  }
}
.applyButton {
  width: 100%;
  height: 0.5rem;
  position: fixed;
  bottom: 0;
  left: 0;
  button {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    background-color: orange;
    color: white;
    font-size: 0.18rem;
  }
}
.erwei {
  width: 2.8rem;
  height: 2.3rem;
  border-radius: 0.2rem;
  .erwei_top {
    width: 100%;
    height: 20%;
    display: flex;
